      <div class="serviceProjectList">
        <figure>
          <img src="../../../assets/img/serviceProjectImg1.jpg" alt="">
          <figcaption>
            门店管理
          </figcaption>
        </figure>
        <figure>
          <img src="../../../assets/img/serviceProjectImg2.jpg" alt="">
          <figcaption>
            会员系统
          </figcaption>
        </figure>
        <figure>
          <img src="../../../assets/img/serviceProjectImg3.jpg" alt="">
          <figcaption>
            预约下单
          </figcaption>
        </figure>
        <figure>
          <img src="../../../assets/img/serviceProjectImg4.jpg" alt="">
          <figcaption>
            营销推广
          </figcaption>
        </figure>
      </div>
    </div>

    <div class="attractCustomer">
      <h3>引客到店</h3>
      <div class="attractList">
        <figure>
          <img src="../../../assets/img/attractImg1.jpg" alt="">
          <figcaption>
            会员营销
          </figcaption>
        </figure>
        <figure>
          <img src="../../../assets/img/attractImg2.jpg" alt="">
          <figcaption>
            砍价活动
          </figcaption>
        </figure>
        <figure>
          <img src="../../../assets/img/attractImg3.jpg" alt="">
          <figcaption>
            裂变礼包
          </figcaption>
        </figure>
      </div>
    </div>
    <index-register></index-register>
  </div>
</template>

<script>

import IndexRegister from '../../../components/indexRegister';

  export default {
    name: 'Service',
    components : {
      IndexRegister
    }
  }
</script>

<style lang='stylus' scoped>
  .service
    width:100%;
    min-width:1200px;
    margin: 0 auto;
    .serviceProject
      background:#f8f8f8;
      width:100%;
      min-width:1200px;
      height:465px;
      text-align:center;
      padding:0.1px;
      h3
        font-family:PingFangSC-Regular;
        font-size:24px;
        color:#303133;
        margin:60px 0 20px 0;
      >p
        font-family:PingFangSC-Regular;
        font-size:16px;
        color:#4a4a4a;
        margin-bottom:60px;
      .serviceProjectList
        width:1200px;
        margin: 0 auto;
        display: -webkit-box; /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
        display: -moz-box; /* 老版本语法: Firefox (buggy) */
        display: -ms-flexbox; /* 混合版本语法: IE 10 */
        display: -webkit-flex; /* 新版本语法: Chrome 21+ */
        display: flex; /* 新版本语法: Opera 12.1, Firefox 22+ */
