<template>
	<div class="indexHeader">
		<header :class="{ 'homeHeader' : activeNumber == 0, 'solutionHeader' : activeNumber == 1, 'channelCooperationHeader' : activeNumber == 2 }">
      <h1 class="clearfixed">
        <figure>
          <img src="../assets/img/logo.png" alt="">
          <figcaption>LIUDU</figcaption>
        </figure>
        <router-link to='/index/home' :class="{ 'activeUrl' : activeNumber == 0 }" @click.native='changePage(0)'>首页</router-link>
        <router-link to='/index/solution' :class="{ 'activeUrl' : activeNumber == 1 }" @click.native='changePage(1)'>解决方案</router-link>
        <router-link to='/index/channelCooperation' :class="{ 'activeUrl' : activeNumber == 2 }" @click.native='changePage(2)'>渠道合作</router-link>
        <div class="buttonDiv">
          <button type="button" @click='login'>登录</button>
          <button type="button" @click='register'>注册</button>
        </div>
      </h1>
      <div class="topInfor" v-show='activeNumber == 0'>
        <p>微商城</p>
        <p>小程序+公众号一体化微信商城</p>
        <p>助力企业快速进入移动社交电商新时代</p>
        <button type="button" name="button" @click='register'>免费注册</button>
      </div>
			<div class="solutionTopInfor" v-show='activeNumber == 1'>
				<h3>全行业解决方案</h3>
				<p>帮助更多垂直行业的商家成功</p>
			</div>
			<div class="channelTopInfor" v-show='activeNumber == 2'>
				<h3>渠道合作代理商火热招募中</h3>
				<p>与代理商伙伴简历共赢发展模式    构建本地化移动互联平台</p>
			</div>
    </header>
    <div class="notice">
      <div class="noticeInfor">
        公告<span><img src="../assets/img/homeContentTop.jpg">六度拼团即将闪亮登场</span>
      </div>
    </div>
	</div>

</template>

<script>
export default {
	name: 'IndexHeader',
	data () {
		return {
			activeNumber : 0
		}
	},
	methods : {
		changePage (num){
			this.activeNumber = num;
		},
		login () {
			window.location.href = '/login/'
		},
		register () {
			window.location.href = '/register/#/register'
		}
	},

}
</script>

<style lang='stylus' scoped>

	.indexHeader
		width:100%;
		min-width:1200px;
		background:#f0f0f0;
		margin:0 auto;
		padding:0.1px;
		opacity:0.95;
		.homeHeader
			background:linear-gradient(44deg, rgba(15,151,253, 0.9),rgba(28,57,195, 0.9)), url("../assets/img/header1.png");
			background-size:100% 100%;
		.solutionHeader
			background:linear-gradient(-9deg, rgba(43,78,191, 0.95),rgba(87,163,255, 0.95)), url("../assets/img/solutionHeaderBack.png");
			background-size:100% 100%;
		.channelCooperationHeader
			background:linear-gradient(-48deg, rgba(76,177,255, 0.95),rgba(173,119,255, 0.95)), url("../assets/img/channelCooperationHeaderBack.png");
			background-size:100% 100%;
		header
			height: 580px;
			background:linear-gradient(44deg, rgba(15,151,253, 0.9),rgba(28,57,195, 0.9)), url("../assets/img/header1.png");
			background-size:100% 100%;
			padding:0.1px;
			h1
				width:1200px;
				height: 60px;
				line-height:60px;
				margin:0 auto;
				margin-top:15px;
        figure
					display:inline-block;
					width:40px;
					text-align:center;
					float:left;
					font-size:0;
					line-height:1;
					margin-right:20px;
          >img
						width:24px;
						height:42px;
          figcaption
						color:#fff;
						font-size:12px;
						height:12px;
						line-height:12px;
						transform: scale(0.833333);
        a
					opacity:0.7;
					font-family:PingFangSC-Light;
					font-size:20px;
					color:#ffffff;
					margin: 0 60px;
				.activeUrl
					opacity:1;
        .buttonDiv
					float: right;
					font-family:PingFangSC-Medium;
					button:first-of-type
						border:1px solid #ffffff;
						border-radius:4px;
						width:91px;
						height:35px;
						background:none;
						font-size:16px;
						color:#ffffff;
					button:last-of-type
						background:#ffffff;
						border-radius:4px;
						width:93px;
						height:35px;
						font-size:16px;
						color:#4a90e2;
						margin-left:10px;
      .topInfor
				width:500px;
				text-align:center;
				margin: 120px auto 0 auto;
        p:first-of-type
					font-family:PingFangSC-Regular;
					font-size:60px;
					color:#ffffff;
        p:nth-of-type(2)
					font-family:PingFangSC-Light;
					font-size:24px;
					color:#ffffff;
					text-align:center;
					margin: 33px 0 10px 0;
        p:nth-of-type(3)
					font-family:PingFangSC-Light;
					font-size:24px;
					color:#ffffff;
					text-align:center;
					margin-bottom:30px;
        button
					background:#ffffff;
					border-radius:4px;
					width:180px;
					height:51px;
					font-family:PingFangSC-Semibold;
					font-size:16px;
					color:#3064d7;
					text-align:center;
					font-weight:bold;
			.solutionTopInfor
				text-align:center;
				h3
					font-family:PingFangSC-Regular;
					font-size:60px;
					color:#ffffff;
					margin:150px 0 22px 0;
				p
					font-family:PingFangSC-Light;
					font-size:24px;
					color:#ffffff;
			.channelTopInfor
				text-align:center;
				h3
					font-family:PingFangSC-Regular;
					font-size:60px;
					color:#ffffff;
					margin-top:150px;
				p
					font-family:PingFangSC-Light;
					width:490px;
					margin:0 auto;
					font-size:24px;
					color:#ffffff;
					text-align:center;
					line-height:44px;
					margin-top:22px;
    .notice
			width:100%;
			min-width:1200px;
      .noticeInfor
				width:1200px;
				margin: 0 auto;
				height:44px;
				line-height:44px;
				font-family:PingFangSC-Regular;
				font-size:18px;
				color:#f5a623;
        span>img
					width:11px;
					height:11px;
					verticle-align:middle;
					margin:0 11px 0 63px;
        span
					font-size:16px;
</style>



