import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Home from '@/pages/home/home'

import Solution from '@/pages/solution/solution'
import Routine from '@/pages/solution/routine/routine'
import Goods from '@/pages/solution/goods/goods'
import Service from '@/pages/solution/service/service'
import MobileNet from '@/pages/solution/mobileNet/mobileNet'

import ChannelCooperation from '@/pages/channelCooperation/channelCooperation'


Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Index',
    component: Index,
    children : [{
      path : '',
      name : 'Home',
      component : Home
    },
    {
      path : 'home',
      name : 'Home',
      component : Home
    },
    {
      path : 'solution',
      name : 'Solution',
      component : Solution,
      children : [
        {
          path : '',
          name : 'Routine',
          component : Routine,
        },
        {
          path : 'routine',
          name : 'Routine',
          component : Routine,
        },
        {
          path : 'goods',
          name : 'Goods',
          component : Goods,
        },
        {
          path : 'service',
          name : 'Service',
          component : Service,
        },
        {
          path : 'mobileNet',
          name : 'MobileNet',
          component : MobileNet,
        },
      ]
    },
    {
      path : 'solution',
      name : 'Solution',
      component : Solution
    },
    {
      path : 'channelCooperation',
      name : 'ChannelCooperation',
      component : ChannelCooperation
    },]
  },
  {
    path: '/index',
    name: 'Index',
    component: Index,
    children : [{
      path : '',
      name : 'Home',
      component : Home
    },
    {
      path : 'home',
      name : 'Home',
      component : Home
    },
    {
      path : 'solution',
      name : 'Solution',
      component : Solution,
      children : [
        {
          path : '',
          name : 'Routine',
          component : Routine,
        },
        {
          path : 'routine',
          name : 'Routine',
          component : Routine,
        },
        {
          path : 'goods',
          name : 'Goods',
          component : Goods,
        },
        {
          path : 'service',
          name : 'Service',
          component : Service,
        },
        {
          path : 'mobileNet',
          name : 'MobileNet',
          component : MobileNet,
        },
      ]
    },
    {
      path : 'solution',
      name : 'Solution',
      component : Solution
    },
    {
      path : 'channelCooperation',
      name : 'ChannelCooperation',
      component : ChannelCooperation
    },]
  }
  ]
})



