<template>
  <div class="mobileNet">
    <div class="mobileSpread">
      <h3>移动网络推广</h3>
      <p>病毒裂变式广告平台，一站式移动广告解决方案</p>
      <div class="figureList">
        <figure class="clearfixed">
          <img src="../../../assets/img/mobileFigureListImg1.png" alt="">
          <figcaption>
            <h4>腾讯社交广告</h4>
            <p>高校实现您的营销目标</p>
            <p>每天有超过10亿用户使用微信、QQ、QQ空间等</p>
            <p>众多腾讯产品及第三方应用</p>
            <p>他们分享自己感兴趣的内容，浏览商业信息</p>
          </figcaption>
        </figure>
        <figure class="clearfixed">
          <img src="../../../assets/img/mobileFigureListImg2.jpg" alt="">
          <figcaption>
            <h4>海量广告资源</h4>
            <p>涵盖95%互联网用户</p>
            <p>对接众多优质移动资源、移动交易平台</p>
            <p>百度、腾讯社交广告、WPS、今日头条、InMobi、360、移动平台联盟等</p>
          </figcaption>
        </figure>
        <figure class="clearfixed">
          <img src="../../../assets/img/mobileFigureListImg3.jpg" alt="">
          <figcaption>
            <h4>精准的定向能力</h4>
            <p>瞄准活跃客户</p>
            <p>提供了众多广告定向条件，可以从中选择适合达成您商业目标</p>
            <p>的条件锁定人群，包括人口属性、商业兴趣、地理位置、使用设备、</p>
            <p>天气环境等等</p>
          </figcaption>
        </figure>
      </div>
    </div>
    <div class="swiperDiv">

      <div class="swiperContainer">
        <img src="../../../assets/img/swiperBack.png" alt="" class="swiperBack">
        <img src="../../../assets/img/swiperNext.png" alt=""  class="swiperNext">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg1.jpg" alt=""></div>
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg2.png" alt=""></div>
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg3.jpg" alt=""></div>
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg4.png" alt=""></div>
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg5.png" alt=""></div>
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg6.png" alt=""></div>
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg7.jpg" alt=""></div>
            <div class="swiper-slide"><img src="../../../assets/img/swiperImg8.png" alt=""></div>
          </div>
        </div>
      </div>

    </div>
    <index-register></index-register>
  </div>
</template>

<script>


import IndexRegister from '../../../components/indexRegister';
import 'element-ui/lib/theme-chalk/index.css';
import Swiper from 'swiper';


  export default {
    name: 'mobileNet',
    components : {
      IndexRegister
    },
    mounted () {
      var swiper = new Swiper('.swiper-container', {
        slidesPerView: 4,
        spaceBetween: 50,
        loop:true,
        navigation: {
          nextEl: '.swiperDiv .swiperNext',
          prevEl: '.swiperDiv .swiperBack',
        },
      });
    }
  }
</script>

<style lang='stylus' scoped>
  .mobileNet
    width:100%;
    min-width:1200px;
    margin: 0 auto;
    .mobileSpread
      background:#f8f8f8;
      width:100%;
      min-width:1200px;
      margin:0 auto;
      height:1782px;
      text-align:center;
      padding:0.1px;
      h3
        font-family:PingFangSC-Regular;
        font-size:24px;
        color:#303133;
        margin:60px 0 20px 0;
      >p
        font-family:PingFangSC-Regular;
        font-size:16px;
        color:#4a4a4a;
        margin-bottom:80px;
      .figureList
        width:1200px;
        margin:0 auto;
        figure
          figcaption
            text-align:left;
            h4
              font-family:PingFangSC-Medium;
              font-size:20px;
              color:#4a4a4a;
              margin-bottom:20px;
              font-weight:bold;
            p
              font-family:PingFangSC-Regular;
              font-size:16px;
              color:#4a4a4a;
              margin-top:14px;
        figure:first-of-type
          img
            width:495px;
            height:435px;
            float:right;
            margin-right:20px;
          figcaption
            margin-left:147px;
            margin-top:106px;
            float:left;
        figure:nth-of-type(2)
          img
            width:432px;
            height:506px;
            float:left;
            margin-left:79px;
          figcaption
            margin-top:199px;
            float:right;
            margin-right:20px;
        figure:last-of-type
          img
            width:615px;
            height:426px;
            float:right;
            margin-top:42px;
          figcaption
            margin-top:215px;
            float:left;
            margin-left:100px;
    .swiperDiv
      height:700px;
      width:100%;
      min-width:1200px;
      padding:0.1px;
      .swiperContainer
        width:1200px;
        margin:0 auto;
        position:relative;
        .swiperBack
          width:50px;
          height:100px;
          position:absolute;
          z-index:999;
          top:40%;
          left:0;
        .swiperNext
          width:57px;
          height:111px;
          position:absolute;
          z-index:999;
          top:40%;
          right:0;
        .swiper-container
          height:552px;
          width:1000px;
          margin:0 auto;
          margin-top:75px;
          .swiper-slide
            text-align:center;
            line-height:552px;
            img
              width:200px;
              height:356px;
              verticle-align:middle;







</style>



