<template>
	<div class="indexRegister">
      <h3>立即注册，免费体验更多解决方案</h3>
      <button type="button" name="button" @click='register'>免费注册</button>
	</div>
</template>

<script>

export default {
	name: 'IndexRegister',
	methods : {
		register () {
			window.location.href = '/register/#/register'
		}
	}
}

</script>

<style lang='stylus' scoped>
	.indexRegister
		width:100%;
		min-width:1200px;
		height:275px;
		background:#46a6ff;
		text-align:center;
		padding:0.1px;
		h3
			font-family:PingFangSC-Medium;
			font-size:24px;
			color:#ffffff;
			margin:90px 0 50px 0;
		button
			background:#ffffff;
			border-radius:4px;
			width:200px;
			height:50px;
			line-height:50px;
			text-align:center;
			font-family:PingFangSC-Semibold;
			font-size:16px;
			color:#46a6ff;
</style>



