<template>
  <div class="solution">
    <div class="solutionTabDiv">
      <figure @click='clickSection(0)'>
        <section :class="{ 'activeSection' : activeNumber == 0 }">
          <img src="../../assets/img/solutionTabImg1.png" alt="">
        </section>
        <figcaption :class="{ 'activeFigcaption' : activeNumber == 0 }">
          <h4>小程序解决方案</h4>
          <p>一键接入，无序开发</p>
        </figcaption>
      </figure>
      <figure @click='clickSection(1)'>
        <section :class="{ 'activeSection' : activeNumber == 1 }">
          <img src="../../assets/img/solutionTabImg2.png" alt="">
        </section>
        <figcaption :class="{ 'activeFigcaption' : activeNumber == 1 }">
          <h4>商品类解决方案</h4>
          <p>售卖或展示实物商品或虚拟商品</p>
        </figcaption>
      </figure>
      <figure @click='clickSection(2)'>
        <section :class="{ 'activeSection' : activeNumber == 2 }">
          <img src="../../assets/img/solutionTabImg3.png" alt="">
        </section>
        <figcaption :class="{ 'activeFigcaption' : activeNumber == 2 }">
          <h4>服务类解决方案</h4>
          <p>适用于线下门店、服务类、o2o类商户</p>
        </figcaption>
      </figure>
      <figure @click='clickSection(3)'>
        <section :class="{ 'activeSection' : activeNumber == 3 }">
          <img src="../../assets/img/solutionTabImg4.png" alt="">
        </section>
        <figcaption :class="{ 'activeFigcaption' : activeNumber == 3 }">
          <h4>移动网络推广</h4>
          <p>高效实现您的营销目标</p>
        </figcaption>
      </figure>
    </div>
    <router-view class="solutionContent"></router-view>
  </div>
</template>

<script>
  export default {
    name: 'Solution',
    data () {
      return {
        activeNumber : 0
      }
    },
    methods : {
      clickSection (num) {
        this.activeNumber = num;
        if(num == 0){
          this.$router.push('/index/solution/routine')
        }
        if(num == 1){
          this.$router.push('/index/solution/goods')
        }
        if(num == 2){
          this.$router.push('/index/solution/service')
        }
        if(num == 3){
          this.$router.push('/index/solution/mobileNet')
        }
      }
    }
  }
</script>

<style lang='stylus' scoped>
  .solution
    width:100%;
    min-width:1200px;
    .solutionTabDiv
      height:286px;
      margin:0 auto;
      width:1200px;
      display: -webkit-box; /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
      display: -moz-box; /* 老版本语法: Firefox (buggy) */
      display: -ms-flexbox; /* 混合版本语法: IE 10 */
      display: -webkit-flex; /* 新版本语法: Chrome 21+ */
      display: flex; /* 新版本语法: Opera 12.1, Firefox 22+ */
      justify-content:space-between;
      figure:last-of-type
        background:none;
      figure
        text-align:center;
        width:25%;
        background:url('../../assets/img/solutionTabIcon.png')no-repeat right 145px;
        background-size: 1px 80px;
        .activeSection
          background:#e6eefc;
        section
          width:66px;
          height:66px;
          margin:80px auto 36px auto;
          line-height:66px;
          border:1px solid rgba(47,114,224,0.3);
          border-radius:50%;
        .activeFigcaption
          color:#2f72e0!important;
        figcaption
          h4
            font-family:PingFangSC-Regular;
            font-size:24px;
            margin-bottom:20px;
          p
            font-family:PingFangSC-Regular;
            font-size:16px;
    .solutionContent
      width:100%;
      min-width:1200px;

</style>



